/*Botones */
.btn {
  margin: auto;
  margin-top: 25px;
  padding: 15px 40px;
  font-size: 18px;
  font-weight: 600;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  text-align: center;
  font-family: Roboto, sans-serif;
  color: #fff;
  background: linear-gradient(90deg, #00091c 0%, #606060 100%);
  transition: 893ms;
  transform: translateY(0);
  display: flex;
  flex-direction: row;
  align-items: center;
}

.btn:hover {
  transition: 893ms;
  padding: 10px 90px;
  transform: translateY(0px);
  background: linear-gradient(90deg, #0066CC 0%, #48cae4 100%);
  color: #0066cc;
}

.btnh {
  margin-right: 15px;
  padding: 15px 40px;
  font-size: 18px;
  font-weight: 600;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  font-family: Roboto, sans-serif;
  color: #fff;
  background: linear-gradient(90deg, #707070 0%, rgba(102, 118, 124, 0.74) 100%);
  transition: 893ms;
  transform: translateY(0);
  align-items: center;
}

.btnh:hover {
  transition: 893ms;
  padding: 15px 45px;
  transform: translateY(0px);
  background: linear-gradient(90deg, #5f9cd5 0%, #96d1e0 100%);
  color: #0066cc;
}

.btn-regresar {
  padding: 10px;
  font-size: 16px;
  font-weight: 400;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  font-family: Roboto, sans-serif;
  color: #444e54;
  background: none;
  transition: 500ms;
  transform: translateY(0);
  align-items: center;
}

.btn-regresar:hover {
  font-size: 18px;
  color: #0066cc;
}

.btnDirect {
  background-color: #d5d5d5;
  background-size: cover;
  background-position: center;
  display: inline-block;
  border: none;
